import React from 'react'
import { FaTimes } from "react-icons/fa";
import {useGlopalContext} from './Context'

function Modal() {
 const {isModalOpen ,setIsModalOpen} = useGlopalContext()
 return (
  <div className = {`${isModalOpen ? 'modal-container active' : 'modal-container'}`} >
   <div className="content">
    <h1>Modal Content</h1>
    <div className="close" onClick={()=>setIsModalOpen(false)}><button className='btn-2'><FaTimes/></button></div>
   </div>
  </div>
 )
}

export default Modal
