import React  from 'react'
import { FaBars } from "react-icons/fa";
import {useGlopalContext} from './Context'

function Home() {
 const {  setIsModalOpen ,setIsSidebarOpen} = useGlopalContext()
 return (
  <div className='homecontainer'>
    <button onClick={()=>setIsSidebarOpen(true) }  className='side-bar-btn'><FaBars/></button>
    <button onClick={()=>setIsModalOpen(true) } className='btn'>show modal</button>
  </div>
 )
}

export default Home
