import React from 'react'
import { FaTimes ,FaHome ,FaUserFriends ,FaFolderOpen,FaRegCalendarAlt ,FaFile ,FaFacebook ,FaTwitter , FaLinkedin ,FaBehance} from "react-icons/fa";
import {useGlopalContext} from './Context'
function Sidebar() {
 const { isSidebarOpen ,setIsSidebarOpen} = useGlopalContext()
 return (
  <div className={`${ isSidebarOpen ? 'sidebar-container side-bar-active' : 'sidebar-container'}`}>
   
   <div className="logo">
    <img src="https://react-projects-12-sidebar-modal.netlify.app/static/media/logo.2bb7da65.svg" alt="logo"/>
    <button className='btn-2' onClick={()=>setIsSidebarOpen(false)}><FaTimes/></button>
   </div>
   <div className="main-links-container ">
    <ul className='main-links'>
    <li><a href="https://google.com"  target='_blanck'> <FaHome className='icon'/> home</a></li>
    <li><a href="https://google.com" target='_blanck'><FaUserFriends className='icon'/> team</a></li>
    <li><a href="https://google.com" target='_blanck'><FaFolderOpen className='icon'/> projects</a></li>
    <li><a href="https://google.com" target='_blanck'><FaRegCalendarAlt className='icon'/> calender</a></li>
    <li><a href="https://google.com" target='_blanck'><FaFile className='icon'/> documents</a></li> 
   </ul>
   </div>
   <div className="social-links">
    <a href="https://google.com" target='_blanck'> <FaFacebook className='icon'/> </a>
    <a href="https://google.com" target='_blanck'> <FaTwitter className='icon'/> </a>
    <a href="https://google.com" target='_blanck'> <FaLinkedin className='icon'/> </a>
    <a href="https://google.com" target='_blanck'> <FaBehance className='icon'/> </a>
   </div>
  </div>
 )
}

export default Sidebar
